<template>
  <div class="create-news">
    <div class="create-news__form">
      <v-input
        :label="'Заголовок новости'"
        :error="$v.form.title.$error"
        v-model="$v.form.title.$model"
        :text-error="'введите заголовок новости'"
      />
      <v-multiselect
        :label="'Категория'"
        :options="CATEGORY"
        :value="form.categories"
        @multi-change="form.categories = $event"
      ></v-multiselect>
      <div
        class="form-control"
        :class="{ 'form-control--error': $v.form.body.$error }"
      >
        <vue-editor
          v-model="$v.form.body.$model"
          :editorToolbar="customToolbar"
        ></vue-editor>
        <label v-show="!$v.form.body.$error">Описание новости</label>
        <label v-show="$v.form.body.$error">введите описание новости</label>
      </div>

      <file-pond
        name="test"
        ref="pond"
        label-idle="Добавить фото"
        :allow-multiple="true"
        accepted-file-types="image/jpeg, image/png"
        imagePreviewHeight="100"
        :allow-reorder="true"
        @updatefiles="handleFilePond"
        @reorderfiles="handleFilePond"
      />

      <div class="create-news__form-group-btn">
        <button
          class="button-primary"
          :disabled="$v.form.$invalid"
          @click="createNews"
        >
          Отправить на проверку
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import { VueEditor } from "vue2-editor";
import qs from "qs";
import { mapActions, mapGetters } from "vuex";

import vueFilePond from "vue-filepond";
import "filepond/dist/filepond.min.css";
import "filepond-plugin-image-preview/dist/filepond-plugin-image-preview.min.css";
import FilePondPluginFileValidateType from "filepond-plugin-file-validate-type";
import FilePondPluginImagePreview from "filepond-plugin-image-preview";
import axios from "axios";
import VMultiselect from "../vMultiselect";
import VInput from "../vInput";

// Create component
const FilePond = vueFilePond(
  FilePondPluginFileValidateType,
  FilePondPluginImagePreview
);

export default {
  name: "CreateNews",
  components: { VInput, VueEditor, FilePond, VMultiselect },
  data() {
    return {
      form: {
        title: "",
        body: "",
        categories: [],
        title_picture: [],
        images: []
      },
      title_picture: {},
      images: [],
      category: [],
      customToolbar: [
        ["bold", "italic", "underline"],
        [{ list: "ordered" }, { list: "bullet" }]
      ]
    };
  },
  validations: {
    form: {
      title: {
        required
      },
      body: {
        required
      }
    }
  },
  mounted() {
    this.GET_CATEGORY();
  },
  methods: {
    ...mapActions("category", ["GET_CATEGORY"]),
    handleFilePond: function() {
      let fileImages = this.$refs.pond.getFiles();
      this.title_picture = fileImages.map(item => item.file)[0];
      this.images = fileImages.map(item => item.file).slice(1);
    },
    async uploadImages() {
      let formDataImages = new FormData();

      this.images.forEach(item => {
        formDataImages.append("file[]", item);
      });

      await axios
        .post(
          `${process.env.VUE_APP_API_URL}/v1/post/upload-images`,
          formDataImages,
          {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          }
        )
        .then(result => {
          this.form.images = result.data.files;
        })
        .catch(error => {
          console.log(error);
        });
    },
    async uploadTitleImage() {
      let formDataTitleImage = new FormData();
      formDataTitleImage.append("file", this.title_picture);

      await axios
        .post(
          `${process.env.VUE_APP_API_URL}/v1/post/upload-title`,
          formDataTitleImage,
          {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          }
        )
        .then(result => {
          this.form.title_picture = result.data[0];
        })
        .catch(error => {
          console.log(error);
        });
    },
    async createNews() {
      await this.uploadTitleImage();
      await this.uploadImages();

      let form = qs.stringify({
        ...this.form,
        categories: this.form.categories.map(item => item.id)
      });

      await axios
        .post(`${process.env.VUE_APP_API_URL}/v1/post/create`, form)
        .then(() => {
          this.$toasted.global.success(
            "Новость создана и отправлена на проверку"
          );
          this.form = {
            title: "",
            body: "",
            title_picture: [],
            categories: [],
            images: []
          };
          this.$refs.pond.removeFiles();
          this.$v.$reset();
        })
        .catch(error => {
          error.data.forEach(item => {
            this.$toasted.global.error(item.message);
          });
        });
    }
  },
  computed: {
    ...mapGetters("category", ["CATEGORY"])
  }
};
</script>

<style scoped></style>
