<template>
  <div class="v-select" v-click-outside="hide">
    <div
      class="v-select__select"
      :class="{ 'v-select__select--error': showError }"
    >
      <div class="v-select__select-wrap" @click="toggle">
        <input
          type="text"
          :id="id"
          :placeholder="placeholder"
          :value="getSelected"
          readonly
        />
        <div class="v-select__icon" :class="{ 'v-select__icon--active': show }">
          <svg>
            <use href="@/assets/img/sprite.svg#icon-arrow-down-select"></use>
          </svg>
        </div>
      </div>
      <label :for="id" v-show="!showError">
        {{ label }}
        <span class="v-select__require" v-show="requireInput">*</span>
      </label>
      <label :for="id" v-show="showError">{{ errorText }}</label>
    </div>
    <div class="v-select__options" v-show="show">
      <div
        class="v-select__options-item"
        v-for="(item, index) in options"
        :key="index"
      >
        <input
          type="radio"
          :id="`option${id}${index}`"
          :value="item"
          v-model="selected"
          @change="change"
        />
        <label :for="`option${id}${index}`">{{ item.title }}</label>
      </div>
    </div>
  </div>
</template>

<script>
import ClickOutside from "vue-click-outside";

export default {
  name: "vSelect",
  props: {
    label: {
      type: String,
      default: ""
    },
    options: {
      type: Array,
      default: () => []
    },
    value: {
      type: Object,
      default: () => {}
    },
    placeholder: {
      type: String,
      default: ""
    },
    showError: {
      type: Boolean,
      default: false
    },
    errorText: {
      type: String,
      default: ""
    },
    requireInput: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      id: null,
      show: false,
      selected: null
    };
  },
  methods: {
    toggle() {
      this.show = !this.show;
    },
    hide() {
      this.show = false;
    },
    change() {
      this.hide();
      this.$emit("select-change", this.selected);
    }
  },
  computed: {
    getSelected() {
      return this.selected !== null ? this.selected.title : "";
    }
  },
  mounted() {
    this.id = this._uid;
    this.selected = this.value;
  },
  watch: {
    value(val) {
      this.selected = val;
    }
  },
  directives: {
    ClickOutside
  }
};
</script>

<style scoped></style>
