<template>
  <div class="page">
    <div class="page__content">
      <div class="page__header">
        <h1 class="page__title">
          Анкета бизнес-партнёра
        </h1>
      </div>
    </div>

    <div class="partner-profile" v-show="load">
      <div class="partner-profile__left-col">
        <div class="partner-profile__photo">
          <svg>
            <use href="@/assets/img/sprite.svg#icon-handshake"></use>
          </svg>
        </div>

        <div class="partner-profile__group-btn" v-if="state === 3">
          <button
            class="button-primary mt-10"
            @click="$router.push('/create-news')"
          >
            <svg>
              <use href="@/assets/img/sprite.svg#icon-monitor"></use>
            </svg>
            Добавить новость
          </button>
        </div>
      </div>
      <form class="partner-profile__form" @submit.prevent>
        <div
          class="resident-profile__info resident-profile__info--warning"
          v-if="state === 2"
        >
          <div class="resident-profile__info-icon">
            <svg>
              <use href="@/assets/img/sprite.svg#icon-info"></use>
            </svg>
          </div>
          <div class="resident-profile__info-text">
            Заявка от {{ userRequest.created_at | fdate }} находится на
            рассмотрении
          </div>
        </div>
        <div
          class="resident-profile__info resident-profile__info--danger"
          v-if="state === 4"
        >
          <div class="resident-profile__info-icon">
            <svg>
              <use href="@/assets/img/sprite.svg#icon-info"></use>
            </svg>
          </div>
          <div class="resident-profile__info-text">
            Заявка от {{ userRequest.created_at | fdate }} отклонена
          </div>
        </div>
        <div
          class="resident-profile__info resident-profile__info--success"
          v-if="state === 3"
        >
          <div class="resident-profile__info-icon">
            <svg>
              <use href="@/assets/img/sprite.svg#icon-info"></use>
            </svg>
          </div>
          <div class="resident-profile__info-text">
            Ваша заявка одобрена
          </div>
        </div>
        <v-input
          :label="'Имя'"
          :error="$v.form.firstname.$error"
          v-model="$v.form.firstname.$model"
          :text-error="'введите имя'"
          read-only
          require-input
        />
        <v-input
          :label="'Фамилия'"
          :error="$v.form.lastname.$error"
          v-model="$v.form.lastname.$model"
          :text-error="'введите фамилию'"
          read-only
          require-input
        />
        <v-input
          :label="'Телефон'"
          :error="$v.form.phone.$error"
          v-model="$v.form.phone.$model"
          :mask="'+7 (###) ###-##-##'"
          :text-error="'введите телефон'"
          read-only
          require-input
        />
        <v-input
          :label="'E-mail'"
          :error="$v.form.email.$error"
          v-model="$v.form.email.$model"
          :text-error="'введите e-mail'"
          read-only
          require-input
        />
        <v-multiselect
          :label="'Тип проекта'"
          :options="CATEGORY"
          :value="form.categories"
          @multi-change="form.categories = $event"
          :show-error="$v.form.categories.$error"
          :error-text="'укажите тип проекта'"
          require-input
        ></v-multiselect>
        <v-select
          :label="'Необходимая площадь'"
          :options="AREA"
          :value="form.area"
          @select-change="form.area = $event"
          :show-error="$v.form.area.$error"
          :error-text="'укажите необходимую площадь'"
          require-input
        ></v-select>
        <v-input :label="'Сайт'" v-model="form.siteUrl" />
        <v-input :label="'Instagram'" v-model="form.instagram_profile" />
        <div class="partner-profile__form-group-btn">
          <button
            class="button-primary"
            @click="onCreatePartner"
            v-if="this.state === 1"
          >
            Отправить на проверку
          </button>
        </div>
      </form>
    </div>

    <modal
      :show.sync="showModalCreateNews"
      :centered="false"
      :show-close="true"
    >
      <create-news></create-news>
    </modal>
  </div>
</template>

<script>
import { required, minLength, email } from "vuelidate/lib/validators";
import VMultiselect from "../components/vMultiselect";
import VSelect from "../components/vSelect";
import Modal from "../components/Modals/Modal";
import CreateNews from "../components/News/CreateNews";
import VInput from "../components/vInput";
import { mapActions, mapGetters } from "vuex";
import axios from "axios";
import qs from "qs";
import mixin from "../mixin/mixin";

export default {
  name: "PartnerProfilePage",
  components: { VInput, CreateNews, VMultiselect, VSelect, Modal },
  mixins: [mixin],
  data() {
    return {
      load: false,
      showModalCreateNews: false,
      form: {
        id: 0,
        username: "",
        firstname: "",
        lastname: "",
        phone: "",
        email: "",
        categories: [],
        area: {},
        siteUrl: "",
        instagram_profile: ""
      },
      userRequest: null
    };
  },
  validations: {
    form: {
      firstname: {
        required
      },
      lastname: {
        required
      },
      phone: {
        required,
        minLength: minLength(18)
      },
      email: {
        required,
        email
      },
      categories: {
        required
      },
      area: {
        required
      }
    }
  },
  async mounted() {
    let load = this.$loading.show();
    try {
      this.load = false;
      const user = this.$store.getters["user/getUser"];
      await this.loadData();
      await this.GET_CATEGORY();
      await this.GET_AREA();
      this.form = {
        ...user,
        phone: user.phone ? String(user.phone).slice(1) : "",
        categories: this.userRequest ? this.userRequest.categories : [],
        area: this.userRequest ? this.userRequest.area[0] : {},
        siteUrl: this.userRequest ? this.userRequest.siteUrl : ""
      };
      this.load = true;
      load.hide();
    } catch (error) {
      this.$toasted.global.error(error.response.data.message);
      load.hide();
    }
  },
  methods: {
    ...mapActions("category", ["GET_CATEGORY"]),
    ...mapActions("area", ["GET_AREA"]),
    async loadData() {
      await axios
        .get(`${process.env.VUE_APP_API_URL}/v1/partner-request/user-request`)
        .then(response => {
          if (response.data.length > 0) {
            this.userRequest = response.data.slice(-1)[0];
          } else {
            this.userRequest = null;
          }
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
          }
          this.$toasted.global.error(error.response.data.message);
        });
    },
    async onCreatePartner() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        let form = qs.stringify({
          ...this.form,
          categories: this.form.categories.map(item => item.id),
          area: this.form.area.id
        });
        await axios
          .post(
            `${process.env.VUE_APP_API_URL}/v1/partner-request/create`,
            form
          )
          .then(response => {
            this.userRequest = response.data;
            this.$toasted.global.success("Данные обновлены");
          })
          .catch(error => {
            if (!error.response) {
              this.$toasted.global.error("Нет соединения с сервером");
            }
            this.$toasted.global.error(error.response.data.message);
          });
      }
    }
  },
  computed: {
    ...mapGetters("category", ["CATEGORY"]),
    ...mapGetters("area", ["AREA"]),
    state() {
      // 1 - заявка не создана, 2 - заявка на рассмотрении, 3 -  заявка одобрена , 4 -  заявка не одобрена
      if (this.userRequest === null) {
        return 1;
      }
      if (this.userRequest && this.userRequest.confirmed === 0) {
        return 2;
      }
      if (this.userRequest && this.userRequest.confirmed === 1) {
        return 3;
      }
      if (this.userRequest && this.userRequest.confirmed === 2) {
        return 4;
      }
      return 0;
    }
  }
};
</script>

<style scoped></style>
